@charset "utf-8";

@import url(reset.css);

/* ********************** 공통영역 선언시작 ************************** */

body{
  font-family: 'arial', sans-serif;
  font-size: 1.4rem;
  color: #2d2d2d;
  overflow-x: hidden;
}

/* Layout Setting */
.container{height: 100%; padding: 105px 0;}
.container>div:first-child{margin-bottom: 63px;}

#navi{margin-bottom: 0;}
#navi .container{padding: 0;}
#team, #skill{background-color: #333; color: #fff;}
#footer{ background-color: #7dcb3d; color: #fff; }

/* Title Setting */
.title{text-align: center;}
.title h2{
  width: 100%; height: 84px; margin-bottom: 42px;
  font: 400 3.6rem/42px 'Nanum Gothic', sans-serif;
  position: relative;
}
.title h2::after{
  content: ''; display: block; width: 170px; height: 3px;
  background-color: #8ec735;
  position: absolute; left: 50%; bottom: 0; margin-left: -85px;
}
.title h2 span{
  color: #8ec735; font-weight: 700; font-family: 'Roboto', sans-serif;
}


/* lightbox-0.5 CSS */
#lightbox-container-image-data-box{box-sizing: content-box;} /*라이트박스의 밑에 사이즈 안맞는현상 맞춰주기*/


/* jarallax CSS */
.jarallax {
  position: relative;
  z-index: 0;
}
.jarallax > .jarallax-img {
  position: absolute;
  object-fit: cover;
  font-family: 'object-fit: cover;';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* Covervid Video CSS */
.covervid-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.covervid-wrapper video{
  object-fit: cover;
}

/* ********************** 개별영역 선언시작 ************************** */
/* ********** Navigation Area Start ************ */
#navi{ background-color: rgba(0, 0, 0, 0.5); border-radius: 0; border: 0;}
#navi .container>div:first-child { margin-bottom: 0; }
#navi a{color: #fff;}

/* Site Logo */
#navi h1{line-height: 0.9;}
#navi h1 a{
  font: 700 1.8rem 'Nanum Gothic', sans-serif; text-decoration: none;
}
#navi h1 a span{color: #8ec735;}


/* gnb */
#navi #gnb li a:hover{color: #8ec735; }



/* ********** Intro Area Start ************ */
#intro{ width: 100%; height: 100vh; position: relative;}
#intro .title-box{
  width: 640px; height: 168px; 
  text-align: center; z-index: 10; color: #fff;
  position: absolute; left: 50%; top: 50%;
  
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

#intro .title-box h2{
  width: 100%; height: 42px; margin-bottom: 63px;
  font: 700 4.8rem/42px 'Roboto', sans-serif;
  animation-delay: 0.5s;
}
#intro .title-box p{
  animation-delay: 1.5s;
}


/* ***************** About Area Start ****************** */

/* left-box Area Start */
#about .left-box img{width: 100%; height: 100%;}

/* right-box Area Start */
#about .right-box{padding-left: 30px;}
#about .right-box h3{
  width: 100%; height: 42px; margin-bottom: 21px;
  font: 400 2.5rem/42px 'Nanum Gothic', sans-serif;
}
#about .right-box h3 span{color: #8ec735; font-weight: 700;}
#about .right-box p{margin-bottom: 63px;}
#about .right-box ul{width: 100%; height: 105px; margin-bottom: 42px; }
#about .right-box ul li{
  width: 100%; height: 21px; margin-bottom: 21px;
  font: 400 1.4rem/21px 'arial', sans-serif;
}
#about .right-box ul li:last-child{margin: 0;}


/* *************** Team Area Start *************** */
#team{text-align: center;}
#team .content img {margin-bottom: 30px;}
#team .content .sns-group {width: 100%; margin-bottom: 42px;}
#team .content .sns-group a {
  color: #fff; margin: 0 2px;
  display: inline-block; width: 42px; height: 42px; line-height: 42px;
  border: 2px solid #fff; border-radius: 50%; font-size: 1.8rem;
}
#team .content .sns-group a:hover {color: #fc0; border-color: #fc0;}
#team .content .job{font-size: 2rem;}
#team .content .job span{display: block; font-size: 1.4rem; color: #adadad;}


/* ***************** Service Area Start ****************** */
#service .content>div{
  width: 23%; height: auto; margin: 0 1% 2%; text-align: center; 
  border: 1px solid #8ec735; padding: 42px 10px; border-radius: 7px;
}
#service .content>div .fa{
  font-size: 4rem; margin-bottom: 21px; color: #8ec735;
}
#service .content>div h3{
  width: 100%; height: 21px; 
  font: 700 1.6rem/21px 'Nanum Gothic', sans-serif;
  margin-bottom: 21px;
}


/* ********** Skill Area Start ************ */
#skill .content>div{text-align: center;}
#skill .content>div h3{
  width: 100%; height: 42px;
  font: 700 2.3rem/42px 'Nanum Gothic', sans-serif;
  margin-bottom: 21px;
}
#skill .content>div>div{position: relative;}
#skill .content>div>div strong{
  font-size: 2rem;
  position: absolute; left: 50%; top: 50%;
  
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}


/* ********** Works Area Start ************ */
#works .title p{ margin-bottom: 63px; }

/* Gmenu Area Start */
#works .gmenu{width: 100%; height: 42px; text-align: center;}
#works .gmenu li{
  display: inline-block; width: 150px; height: 42px;
}
#works .gmenu li a{
  display: block; width: 100%; height: 100%; 
  font: 500 1.4rem/42px 'Nanum Gothic', sans-serif;
  background-color: #fc0; border: 1px solid #888;
  color: #2d2d2d; text-decoration: none;
}
#works .gmenu li a:hover{background-color: #fca000;}

/* 메뉴 활성화 클래스 */
.menu-act{ background-color: #fca000 !important;}


/* Gallery Area Start */
#works #gallery{width: 100%; height: auto;}
#works #gallery>div{width: 23%; height: 189px; margin: 0 1% 2%;}


/* ********** Banner Area Start ************ */
#banner{
  background-image: url(../images/lg/banner-bg.jpg);
  text-align: center; color: #fff;
}
#banner .img-box{margin-bottom: 80px;}
#banner p{margin-bottom: 63px;}
#banner .name-box{
  width: 100%; height: 63px;
  font: 700 1.6rem 'Nanum Gothic', sans-serif;
}
#banner .name-box span{
  font-size: 1.4rem; font-style: italic; color: #ccc; display: block;
  margin-top: 15px;
}

/* ********** Map Area Start ************ */
#map_canvas{width: 100%; height: 357px;}


/* ********** Contact Area Start ************ */
#contact form>div{
  width: 100%; height: auto; display: flex; margin-bottom: 27px; 
}
#contact form>div>div{
  width: 100%; height: 35px; flex: 1; margin: 0; 
}
#contact form>div>div:nth-child(2){margin: 0 1%;}
#contact form>p{margin-top: 60px; text-align: center; }
#contact form>div>div input, #contact form textarea{
  width: 100%; border: 1px solid #ccc;
  background-color: #f2f2f2; border-radius: 5px;
  text-indent: 10px;
}
#contact form>div>div input{height: 100%;}
#contact form textarea{height: 145px;}

#contact form>p{margin-top: 60px; text-align: center;}
#contact form>p input{
  display: inline-block; width: 200px; height: 42px;
  border: 1px solid #8ec735; color: #8ec735;
  font: 700 1.6rem/42px 'Roboto', sans-serif;
  cursor: pointer; background-color: #fff;
}
#contact form>p input:hover{
  background-color: #8ec735; color: #fff;
}


/* ***************** Footer Area Start ****************** */
#footer{height: 105px; text-align: center;}
#footer .container{padding: 21px 0 0;}
#footer .sns-group{width: 100%; height: 42px; margin-bottom: 10px;}
#footer .sns-group a{
  font-size: 3.4rem; color: #fff; margin: 0 5px;
}
#footer p{width: 100%; height: 21px; line-height: 21px;}



/* ***************** Media Query Area Start ****************** */

/* ************ Desktop Area Start ************* */
@media screen and ( min-width: 960px ){

  /* ************* 공통영역 선언시작 ************** */
  .container{width: 940px; padding: 105px 10px;}

  /* Bootstrap Settings */
  .col-md-7{width: 58.3333333%; }
  .col-md-5{width: 41.6666667%;}
  .col-sm-6 {width: 25%;}


  /* ************* 개별영역 선언시작 ************** */
  /* header Area Start */
  #navi .containerP{padding: 0 10px;}

  /* works Area Start */
  #works #gallery>div{width: 29.3%; margin: 0 2% 4%;}
  #works #gallery>div img{width: 100%; height: 100%;}
}



/* ************ Tablet And Desktop Area Start ************* */
@media screen and (min-width: 768px) and (max-width: 959px){

  /* ************* 개별영역 선언시작 *********** */

  /* About Area Start */
  #about .left-box{margin-bottom: 63px;}

  /* Team Area Start */
  #team .content>div:nth-child(1),
  #team .content>div:nth-child(2){margin-bottom: 63px;}

  /* Service Area Start */
  #service .content>div{ width: 48%; }

  /* Skill Area Start */
  #skill .circle1, #skill .circle2{margin-bottom: 42px;}

  /* works Area Start */
  #works #gallery>div{width: 29.3%; margin: 0 2% 4%;}
  #works #gallery>div img{width: 100%; height: 100%;}

  /* Banner Area Start */
  #banner p{padding: 0 30px;}

  /* Contact Area Start */
  #contact form{padding: 0 30px;} 

}


/* ************ Tablet Area Start ************* */
@media screen and (max-width: 767px){

  /* ************* 공통영역 선언시작 ************** */
  /* Bootstrap Setting */
  .container-fluid>.navbar-collapse, 
  .container-fluid>.navbar-header, 
  .container>.navbar-collapse, 
  .container>.navbar-header{margin: 0;}

  /* Bootstrap Settings */
  .col-sm-6 {width: 50%;}

  .container{
    width: 100% !important; padding: 105px 10px !important;
  }
  .navbar-toggle{margin-right: 0;}


  /* ************* 개별영역 선언시작 ************** */

  /* Navigation Area Start */
  #navi .container{padding: 0 10px !important;}

  
  /* Intro Area Start */
  #intro{
    background: url(../images/sm/intro-bg.png) no-repeat top center;
    background-size: cover;
  }
  #intro .covervid-wrapper{display: none;}


  /* About Area Start */
  #about .left-box{margin-bottom: 63px;}


  /* Team Area Start */
  #team .content>div:nth-child(1),
  #team .content>div:nth-child(2){margin-bottom: 63px;}


  /* Service Area Start */
  #service .content>div{width: 42%; margin: 0 4% 3%;}


  /* skill Area Start */
  #skill .circle1, #skill .circle2{margin-bottom: 63px;}


  /* Works Area Start */
  #works .gmenu li{width: 18%; margin: 0 0.5%; }
  #works #gallery>div{width: 40%; margin: 0 5% 4%;}
  #works #gallery>div img{width: 100%; height: 100%;}

  /* Contact Area Start */
  #contact form{padding: 0 30px}
  #contact form>div:first-child{flex-direction: column;}
  #contact form>div:first-child>div{margin: 0 0 21px;}


  /* footer Area Start */
  #footer .container{padding: 15px 10px 0 !important;}

}



/* ************ Mobile Area Start ************* */
@media screen and (max-width: 600px){
  /* Intro Area Start */
  #intro .title-box{
    width: 100%; left: 0; padding: 0 30px;
    
    -webkit-transform: translate(0,-50%);
    -moz-transform: translate(0,-50%);
    -o-transform: translate(0,-50%);
    transform: translate(0,-50%);
  }
  #intro .title-box h2{
    font-size: 3.8rem; font-weight: 500; height: auto;
    margin-bottom: 63px;
  }

  /* works Area Start */
  #works .gmenu{width: 100%; height: auto; padding: 0 20%;}
  #works .gmenu li{
    width: 100%; float: none; margin-bottom: 21px;
  }
  #works #gallery>div{ width: 45%; margin: 0 2.5% 4%; }

  /* Google Map */
  #map_canvas{display: none;}


}

@media screen and (max-width: 500px){
  /* ************* 공통영역 선언시작 ************** */

  /* Title Setting */
  .title h2{width: 100%; height: 105px; font-size: 3rem; }
  .title h2 span{display: block;}

  /* Bootstrap Setting */
  .col-md-7,
  .col-md-5,
  .col-sm-6 {width: 100% !important;}
  .col-sm-6 {margin-bottom: 63px !important;}


  /* ************* 개별영역 선언시작 ************** */
  /* Team Area Start */
  #team .content>div:last-child{margin-bottom: 0 !important;}
  
  /* Service Area Start */
  #service .content{padding: 0 10%;}
  #service .content>div{width: 100% !important; margin: 0; }
  #service .content>div:last-child{margin-bottom: 0 !important;}

  /* Skill Area Start */
  #skill .col-sm-6{margin-bottom: 63px !important;}
  #skill .col-sm-6:last-child{margin-bottom: 0 !important;}
  #skill .circle1, #skill .circle2{margin-bottom: 0 !important;} 

  /* Works Area Start */
  #works #gallery{width: 60%; margin: auto;}
  #works #gallery>div{width: 100%; margin-bottom: 21px;}

}